import React from 'react'
import Link from 'gatsby-link'

const Menu = (props) => (
    <nav id="menu">
        <div className="inner">
            <ul className="links">
                <li><Link onClick={props.onToggleMenu} to="/">Home</Link></li>
                <li><Link onClick={props.onToggleMenu} to="/generic">Performace</Link></li>
                <li><Link onClick={props.onToggleMenu} to="/generic">Multi-Device Support</Link></li>
                <li><Link onClick={props.onToggleMenu} to="/generic">Base Technologies</Link></li>
                <li><Link onClick={props.onToggleMenu} to="/generic">Clean Design</Link></li>
                <li><Link onClick={props.onToggleMenu} to="/generic">Product and Pricing</Link></li>
            </ul>
            <ul className="actions vertical">
                <li><Link onClick={props.onToggleMenu} to="#contact" className="button fit">Contact</Link></li>
            </ul>
        </div>
        <a className="close" onClick={props.onToggleMenu} href="javascript:;">Close</a>
    </nav>
)

Menu.propTypes = {
    onToggleMenu: React.PropTypes.func
}

export default Menu



